                <i class="el-icon-s-flag" style="color: red"></i>
              </template>
              <template slot="suffix">
                <i class="el-icon-s-flag" style="color: blue"></i>
              </template>
            </el-statistic>
          </div>
        </el-col>
        <el-col :span="6">
          <div>
            <el-statistic title="活跃用户数">
              <template slot="formatter">
                {{activeuser}}
              </template>
            </el-statistic>
          </div>
        </el-col>

      </el-row>
    </el-card>

  </div>
</template>

<script>
export default {
  data() {
    return {
      like: true,
      value1: 131,
      value2: 1314,
      title: "增长人数",
      activeuser:500
    };
  },
};
</script>
